export default {
    data() {
        return {
            deposit: '---',
            balance: '--'
        }
    },
    methods: {
        topup() {
            this.$router.push({
                path: '/topup'
            });
        },
        refund() {
            this.$http({
                method:'post',
                url: `${BASE_URL}/lock-account/payment?refund=true`,
                data: {}
            }).then(() => {
                USER.isPaidDeposit = false;
                this.$vux.toast.show({
                    text: '成功提交退押金请求'
                });
                this.fetchData();
            });
        },
        fetchData() {
            this.$http({
                url: `${BASE_URL}/lock-account/balance`
            }).then(function(result) {
                const data = result.data;
                if (data.status == 0) {
                    this.deposit = data.deposit/100;
                    this.balance = data.balance/100;
                } else {
                    this.$vux.toast.show({
                        type: 'warn',
                        text: '获取数据失败'
                    })
                }
            }.bind(this)).catch(function(e) {
                var msg = '';
                if (e.response) {
                    const { status, error } = e.response.data;
                    msg = error;
                } else if (e.request) {
                    msg = e.request;
                } else {
                    msg = e.message;
                }
                this.$vux.toast.show({
                    type: 'warn',
                    text: msg
                })
            }.bind(this));
            
        }
    },
    created() {
        this.fetchData();
    }
}



